*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.to-do{
    display: flex;
    justify-content: center;
    margin: 1cm 10px;
}

.to-do-list{
    padding: 10px;
    border: 1px solid orange;
    border-radius: 20px;
}

.name{
    font-size: 30px;
    letter-spacing: 1px;
    margin: 10px;
    color: orange;
}

.input{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.text{
    height: 30px;
    width: 100%;
    border-radius: 5px 0px 0px 5px;
    border: 1px solid rgb(186, 183, 183);
    padding-left: 8px;
}

.add{
    background-color: orange;
    padding: 0px 13px;
    border-radius: 0px 7px 7px 0px;
    font-size: 25px;
    cursor: pointer;
}

.list{
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-top: 10px;
}

.items{
    display: flex;
    flex-flow: wrap row;
    justify-content: space-between;
    align-items: center;
    border: 1px solid orange;
    border-radius: 10px;
    padding: 5px 5px 5px 8px;
}

.item-name{
    list-style: none;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-transform: capitalize;
}

.trash{
    font-size: larger;
    cursor: pointer;
}

.delete-lists{
    text-align: center;
    margin-top: 20px;
    background-color: orange;
    padding: 10px;
    color: white;
    font-size: 0.5cm;
    letter-spacing: 1px;
    border-radius: 10px;
}

.no-item{
    text-align: center;
    margin-top: 10px;
    color: orange;
}